/**
 * @preserve date-and-time.js locale configuration
 * @preserve Russian (ru)
 * @preserve It is using moment.js locale configuration as a reference.
 */

var ru = function (date) {
    var code = 'ru';

    date.locale(code, {
        res: {
            MMMM: ['', '', '', '', '', '', '', '', '', '', '', ''],
            MMM: ['', '', '', '', '', '', '', '', '', '', '', ''],
            dddd: ['', '', '', '', '', '', ''],
            ddd: ['', '', '', '', '', '', ''],
            dd: ['', '', '', '', '', '', ''],
            A: ['', '', '', '']
        },
        formatter: {
            A: function (d) {
                var h = d.getHours();
                if (h < 4) {
                    return this.res.A[0];   // 
                } else if (h < 12) {
                    return this.res.A[1];   // 
                } else if (h < 17) {
                    return this.res.A[2];   // 
                }
                return this.res.A[3];       // 
            }
        },
        parser: {
            h12: function (h, a) {
                if (a < 2) {
                    return h;   // , 
                }
                return h > 11 ? h : h + 12; // , 
            }
        }
    });
    return code;
};

export { ru as default };
